:root {
  --container: 576px;
  --background: #dbdbdb;
  --background_section: #ffffff;
  --margin_img: 26.6px 24.78px 24.6px 28.78px;
  --size_img: 128px;
  --color_a: #007ae9;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: var(--background);
}

.container {
  width: 100%;
  max-width: var(--container);
}

.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
  padding-left: 10px;
  padding-right: 10px;
}

.section_one {
  display: flex;
  flex-flow: row;
  width: 400px;
  height: 128px;
  border-radius: 16px;
  background-color: var(--background_section);
  margin: 0px 78px 32px 98px;
  margin-top: 111px;
}
@media screen and (min-width: 768px) {
  .section_one {
    flex-direction: column;
    width: 318px;
    height: 400px;
  }
}
@media screen and (min-width: 1280px) {
  .section_one {
    flex-direction: row;
    width: 1002px;
    height: 400px;
    margin: 111px 139px 32px 139px;
  }
}

.item_1 {
  background-color: #bcd1ff;
  width: var(--size_img);
  height: var(--size_img);
  border-radius: 16px 0px 0px 16px;
}
.item_1 img {
  margin: var(--margin_img);
  width: 74px;
  height: 76px;
}

.item_2 p {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 11px;
  line-height: 15px;
  color: #6e798c;
  margin-top: 25px;
  margin-left: 30px;
}
@media screen and (min-width: 768px) {
  .item_2 p {
    margin-top: 15px;
  }
}
@media screen and (min-width: 1280px) {
  .item_2 p {
    margin-top: 32px;
    height: 27px;
    left: 435px;
    top: 32px;
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 27px;
    display: flex;
    justify-content: space-between;
  }
  .item_2 p span {
    width: 89px;
    height: 25px;
    font-family: "Open Sans";
    font-style: normal;
    font-weight: 400;
    line-height: 25px;
    display: flex;
    flex-direction: row;
  }
}
.item_2 p span {
  padding-left: 55px;
  font-size: 11px;
}
.item_2 h1 {
  font-family: "DM Serif Display";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 25px;
  color: #081f32;
  margin-left: 30px;
}
@media screen and (min-width: 1280px) {
  .item_2 h1 {
    font-size: 40px;
    line-height: 46px;
  }
}

.item_text {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 19px;
  color: #374a59;
  margin: -10px 25px 0px 24px;
  display: none;
}
@media screen and (min-width: 1280px) {
  .item_text {
    font-size: 18px;
    line-height: 27px;
    width: 527px;
    height: 108px;
  }
}

.footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0px 25px 16px 24px;
  display: none;
}
@media screen and (min-width: 768px) {
  .footer {
    display: flex;
  }
}
@media screen and (min-width: 1280px) {
  .footer {
    font-size: 18px;
    line-height: 25px;
    margin-top: 35px;
  }
  .footer .flex-end {
    margin-left: 238px;
  }
}
.footer a {
  color: var(--color_a);
  padding-left: 8px;
}

